<template>
  <div id="find" style="background:#ccc; height:100vh">
    <head_s>
      <div slot="type"></div>
    </head_s>
    <icon class="icon_s" v-for="i in list">
      <div slot="icon">
        <svg class="icon" aria-hidden="true">
          <use :xlink:href="i.icon" />
        </svg>
      </div>
      <div slot="title">{{i.title}}</div>
      <div slot="more">></div>
    </icon>
  </div>
</template>

<script>
import head_s from "./../../template/head";
import icon from "./../../template/icon";
export default {
  components: {
    head_s,
    icon
  },
  data() {
    return {
      list: [
        { icon: "#icon-pengyouquan", title: "" },
        { icon: "#icon-saoyisao", title: "" },
        { icon: "#icon-yaoyiyao-copy", title: "" },
        { icon: "#icon-fujinderenicon-copy", title: "" },
        { icon: "#icon-piaoliuping-copy", title: "" },
        { icon: "#icon-xiaochengxu-copy", title: "" }
      ]
    };
  }
};
</script>

<style>
#find .icon_s:nth-of-type(odd) .right {
  border-bottom: 1px solid #ccc;
}
#find .icon_s:nth-of-type(odd) {
  margin-top: 0.75rem;
}
#find .icon_s {
  background: #fff;
  position: relative;
}
#find .icon {
  font-size: 0.65em;
  position: relative;
  top: -0.5rem;
}
</style>


